#AboutMe {
	text-align: center;
	font-size: 5vw;
	padding-top: 3rem;
	margin-top: 0;
	margin-bottom: 2vh;
	color: rgb(113, 107, 175);
	text-shadow: 0.5rem 0.5rem 0.5rem rgba(44, 51, 76, 0.184);
}

@media (max-width: 1800px) {
	#AboutMe {
		margin-bottom: 1vh;
	}
	#AboutMe-section {
		grid-template-rows: repeat(3, 33.3vh);
	}
	#AboutMe-section img {
		grid-row-start: 1;
		grid-row-end: span 2;
		grid-column-start: 1 !important;
		grid-column-end: span 4 !important;
		width: 75% !important;
		margin: auto !important;
	}
	#AboutMe-section p {
		grid-row-start: 3;
		grid-column-start: 1 !important;
		margin-left: 10vw !important;
		margin-right: 10vw !important;
		margin-top: 0 !important;
		margin-bottom: 2vh !important;
		text-align: center !important;
	}
}

@media (max-width: 1050px) {
	#AboutMe-section p {
		font-size: 2vw !important;
	}
}

@media (max-width: 275px) and (max-height: 300px) {
	#AboutMe-section p {
		margin-bottom: auto !important;
	}
}

#AboutMe-section {
	background-color: rgba(102, 131, 237, 0.238);
	display: grid;
	grid-template-columns: repeat(4, 25vw);
}

#AboutMe-section img {
	margin-top: 1vh;
	margin-bottom: 1vh;
	margin-left: 5rem;
	grid-column-start: 1.5;
	grid-column-end: 2;
	object-fit: cover;

	min-width: 10rem;
	height: 50vh;
	width: 50rem;
	border-radius: 1rem;
}

#AboutMe-section p {
	margin-top: auto;
	margin-bottom: auto;
	margin-right: 5rem;
	font-size: 1.25vw;
	grid-column-start: 3;
	grid-column-end: 5;
	text-align: left;
	overflow: scroll;
	padding: 1rem;
	border-radius: 1rem;
	background-color: rgba(210, 194, 248, 0.252);
}

.styled-text {
	color: rgb(86, 42, 243);
	font-weight: 500;
	font-style: italic;
}
