#header {
	margin-top: 22vh;
	margin-bottom: 25vh;
	text-align: center;
	display: grid;
	grid-template-columns: repeat(4, 25vw);
}

@media (max-width: 1500px) {
	#header {
		margin-top: 5vh;
		grid-template-rows: repeat(3, 33.3vh);
	}
	#header img {
		grid-column-start: 1 !important;
		grid-column-end: span 4 !important;
		width: 75% !important;
		max-width: 60vw;
		max-height: 50vh;
		margin-left: auto !important;
		margin-right: auto !important;
		margin-top: auto !important;
		margin-bottom: 5vh !important;
	}
	#header h3 {
		grid-row-start: 3;
		grid-column-start: 1 !important;
		grid-column-end: span 4 !important;
		font-size: 7vw !important;
		margin-top: 0 !important;
		margin-left: auto !important;
		margin-right: auto !important;
		text-align: center !important;
	}

	#header h5 {
		grid-row-start: 3;
		margin-top: 10vw !important;
		margin-left: auto !important;
		margin-right: auto !important;
		grid-column-start: 1 !important;
		grid-column-end: span 4 !important;
		font-size: 4vw !important;
		text-align: center !important;
	}
}

#header img {
	width: 100%;
	height: 50vh;
	border-radius: 15%;
	grid-column-start: 2;
	grid-column-end: 3;
	grid-row-start: span 2;
	object-fit: cover;
	box-shadow: 0.85rem 0.75rem 3rem #4b5d8fb5;
}

#header h5 {
	text-align: left;
	margin-top: 0;
	margin-left: 8rem;
	margin-bottom: auto;
	font-size: 30pt;
	grid-column-start: 3;
	grid-column-end: span 2;
	cursor: pointer;
	color: rgba(126, 215, 248, 0.873);
}

#header h3 {
	margin-top: auto;
	margin-bottom: 0;
	margin-left: 3rem;
	font-size: 60pt;
	grid-column-start: 3;
	grid-column-end: span 2;
	text-align: left;
	cursor: pointer;

	/* Fallback: Set a background color. */
	background-color: #18c873;

	/* Create the gradient. */
	background-image: linear-gradient(
		45deg,
		#5c73f3 16.333%,
		#99d6ff 16.333%,
		#99a8ff 33.333%,
		rgb(103, 167, 210) 33.333%,
		rgb(86, 42, 243) 50%,
		rgb(120, 58, 245) 50%,
		rgb(195, 80, 248) 70%
	);

	/* Set the background size and repeat properties. */
	background-size: 100%;
	background-repeat: repeat;
	background-clip: text;

	/* Use the text as a mask for the background. */
	/* This will show the gradient as a text color rather than element bg. */
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;

	/* Animate the text when loading the element. */
	/* This animates it on page load and when hovering out. */
	animation: rainbow-text-simple-animation-rev 0.75s
		cubic-bezier(0.76, 0.61, 0, 1.09) forwards;
}

#header h3:hover {
	animation: rainbow-text-simple-animation 0.75s
		cubic-bezier(0.76, 0.61, 0, 1.09) forwards;
}
/* Move the background and make it smaller. */
/* Animation shown when entering the page and after the hover animation. */
@keyframes rainbow-text-simple-animation-rev {
	0% {
		background-size: 650%;
	}
	40% {
		background-size: 650%;
	}
	100% {
		background-size: 100%;
	}
}
@keyframes rainbow-text-simple-animation {
	0% {
		background-size: 100%;
	}
	40% {
		background-size: 650%;
	}
	100% {
		background-size: 650%;
	}
}
